let showAnswerButton, jokeAnswer, jokeText;

function newJoke() {
  const joke = data[Math.floor(Math.random()*data.length)];
  jokeText.innerHTML = joke["attributes"]["field_joke_opener"];
  jokeAnswer.innerHTML = joke["attributes"]["field_joke_response"];
  showAnswerButton.style.display = '';
  jokeAnswer.style.display = 'none';
}

function init() {
  showAnswerButton = document.getElementById("show-answer-container");
  jokeAnswer = document.getElementById("joke-answer");
  jokeText = document.getElementById("joke-text");
  newJoke();
  document.getElementById("show-answer").addEventListener("click", event=>{
    showAnswerButton.style.display = 'none';
    jokeAnswer.style.display = 'block';
  });

  document.getElementById("next-joke").addEventListener("click", newJoke);
}

document.addEventListener('DOMContentLoaded', init, false);
